//common
#body {
  .position-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  // 
  ::-webkit-scrollbar {
    width: 10px;
  }
  //  .
  ::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 10px;
  }
  .clear {
    &::after {
      content: "";
      display: block;
      clear: both;
    }
  }
  .fr {
    float: right;
  }
  .inline-block {
    display: inline-block;
  }
  .flex-column {
    display: flex;
    flex-direction: column;
    .all-remain {
      flex-grow: 2;
    }
  }
}
